import React from "react";
import { Helmet } from "react-helmet-async";

const Seo = ({ title }) => {
  return (
    <Helmet>
      <title>{title} || EB</title>
      <meta
        name="description"
        content="Made with love"
      />
      <meta property="og:title" content="This is my portfolio" />
    </Helmet>
  );
};

export default Seo;