import React from "react";
import Slider from "react-slick";
import { Link } from "react-router-dom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const GalleryCarousel = ({portfolioContent}) => {
  //   slider settings
  const settings = {
    dots: false,
    arrows: true,
    centerPadding: "0px",
    slidesToShow: portfolioContent.length >= 4 ? 4 : 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3500,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  return (
    <>
      {" "}
      <Slider {...settings} arrows={true}>
        {portfolioContent.length > 1 && portfolioContent.map((item, i) => (
          <div className="item" data-index={i} key={i}>
            <div className="gallery-item ">
              <div className="img-holder">
                <img
                  src={item.img}
                  alt={i}
                  className="img-meta w-100 tran5s"
                  style={{width: 450, height: 450}}
                />
                <div className="caption d-flex justify-content-center align-items-center flex-column">
                  <span className="tag">{item.meta}</span>
                  <h6>
                    <Link to={item.link} className="pj-title">
                      {item.title}
                    </Link>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        ))}
        {" "}
      </Slider>{" "}
    </>
  );
};

export default GalleryCarousel;