import React from "react";

const ContactForm = ({ className = "" }) => {
  const handleSubmit = (event) => {
    event.preventDefault();
  };

  const choice = Math.floor(Math.random() * 500);
  return (
    <>
      <form onClick={handleSubmit}>
        <div className="row controls">
          <div className="col-12">
            <div className="input-group-meta form-group mb-25">
              <label>Name*</label>
              <input
                type="text"
                placeholder={names[choice]}
                name="name"
                required
              />
              <div className="help-block with-errors"></div>
            </div>
          </div>
          {/* End .col */}

          <div className="col-12">
            <div className="input-group-meta form-group mb-40">
              <label>Email*</label>
              <input
                type="email"
                placeholder={names[choice].replaceAll(' ', '')+'@gmail.com'}
                name="email"
                required
              />
              <div className="help-block with-errors"></div>
            </div>
          </div>
          {/* End .col */}

          <div className="col-12">
            <div className="input-group-meta form-group flex-column d-flex mb-40">
              <label>Subject*</label>
              <input
                type="text"
                placeholder="What's on your mind?"
                name="subject"
                required
              />
              <div className="help-block with-errors"></div>
            </div>
          </div>
          {/* End .col */}

          <div className="col-12">
            <div className="input-group-meta form-group mb-35">
              <label>Message*</label>
              <textarea
                placeholder="This doesn't work yet. Please use my email above. I am trying to release Formula Onedget and this will be top priority once that is done."
                name="message"
                required
              ></textarea>
              <div className="help-block with-errors"></div>
            </div>
          </div>
          {/* End .col */}

          <div className="col-12">
            <button className={`theme-btn-one ripple-btn w-100 ${className}`}>
              Send Message
            </button>
          </div>
          {/* End .col */}
        </div>
      </form>
    </>
  );
};

export default ContactForm;

